/*
*
* Mailchimp form
*
*/

#mc_embed_signup a {
    text-decoration: underline;
}

#mc_embed_signup {
    width: 100%;
    box-sizing: border-box;
    padding: 1em;
    color: #666666;
}

#mc_embed_signup .group_halfwidth {
    width: 50%;
    box-sizing: border-box;
    padding: 1em;
    color: #666666;
}

#mc_embed_signup .group_halfwidth.left {
    float: left;
}

#mc_embed_signup .group_halfwidth.right {
    float: right;
}

#mc_embed_signup {
    font-size: .9em;
    text-align: left;
}

#mc_embed_signup .asterisk {
    color: #ff4400;
}

#mc_embed_signup label {
    display: block;
    text-transform: uppercase;
    margin-bottom: .5em;
    cursor: pointer;
    color: #eeeeee;
}

#mc_embed_signup label.nouppercase {
    text-transform: none;
}

#mc_embed_signup input {
    cursor: pointer;
}

#mc_embed_signup input[type="text"], #mc_embed_signup input[type="email"] {
    border: 0;
    padding: .5em;
    background-color: #000000;
    width: 100%;
}

#mc_embed_signup input[type="radio"] + label {
    display: inline;
}

#mc_embed_signup strong {
    text-transform: uppercase;
    display: block;
    margin-bottom: .5em;
}

#mc_embed_signup input[type="submit"] {
    display: block;
    width: 96.3%;
    margin: 1em;
    border: 0;
    box-sizing: border-box;
    padding: .5em;
    background-color: #000000;
    color: #ffffff;
    transition: .05s all linear;
}

#mc_embed_signup input[type="submit"]:hover {
    background-color: #4a0000;
}

#mc_embed_signup select {
    border: 0;
    cursor: pointer;
    padding: .5em;
    background-color: #000000;
    width: 100%;
    color: #eeeeee;
}

#mc_embed_signup input {
    color: #eeeeee;
}


[type="radio"]:checked,
[type="radio"]:not(:checked) {
    position: absolute;
    left: -9999px;
}

[type="radio"]:checked + label,
[type="radio"]:not(:checked) + label {
    position: relative;
    padding-left: 1.25em;
    cursor: pointer;
    line-height: 1em;
    display: inline-block;
    color: #666;
}

[type="radio"]:checked + label:before,
[type="radio"]:not(:checked) + label:before {
    content: '';
    position: absolute;
    left: 0;
    top: 0.1em;
    width: .8em;
    height: .8em;
    border: 1px solid #666666;
    border-radius: 100%;
    background: #000000;
}

[type="radio"]:checked + label:after,
[type="radio"]:not(:checked) + label:after {
    content: '';
    width: 0.43em;
    height: 0.40em;
    background: #ff0000;
    position: absolute;
    top: 0.345em;
    left: 0.23em;
    border-radius: 100%;
    -webkit-transition: all 0.1s ease-out;
    transition: all 0.1s ease-out;
}

[type="radio"]:not(:checked) + label:after {
    opacity: 0;
    -webkit-transform: scale(0);
    transform: scale(0);
}

[type="radio"]:checked + label:after {
    opacity: 1;
    -webkit-transform: scale(1);
    transform: scale(1);
}

#mc_embed_signup .remember li:before {
    content: "\0025E2\00a0";
}

#mc_embed_signup .remember li {
    color: #666666;
    margin-bottom: .5em;
}

#mc_embed_signup li {
    color: #eeeeee;
}

#mc_embed_signup li * {
    color: inherit;
}

#mc_embed_signup .content__gdpr, #mc_embed_signup .content__gdprLegal {
    font-size: .8em;
}

#mc_embed_signup p {
    margin-bottom: 1em;
}

#mc_embed_signup .content__gdpr .important {
    font-weight: 800;
    color: #880000;
}


#mc_embed_signup .mc-field-group {
    margin-bottom: 1em;
}

#mc_embed_signup .response {
    margin: 1em 0 1em;
}

#mc_embed_signup #mce-success-response {
    color: #22aa00;
}

#mc_embed_signup #mce-error-response {
    color: #aa2200;
}

